import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';

import Workout from '../../components/workout';
import WorkoutSummary from '../../components/workout/summary';
import LineGraph from '../../components/lineGraph';

const MONTHS = ['jan', 'feb', 'mar', 'apr', 'mai', 'jun', 'jul', 'aug', 'sep', 'okt', 'nov', 'des'];

const initialState = {
	isLoading: false,
};

@observer
class Teams extends Component {
    toggleTeam = (team) => {
		const { callback } = this.props;
        const { workoutStore } = this.props.stores;
		const { currentTeam } = workoutStore;
		if (team === currentTeam) {
			workoutStore.setCurrentTeam(undefined);
		} else {
			workoutStore.setCurrentTeam(team);
		}
		if (util.isDefined(callback)) {
			callback();
		}
	}

    render() {
        const { userStore, workoutStore } = this.props.stores;
        const { teams } = userStore;
		const { currentTeam } = workoutStore;
        return (
			<div class='container-fluid px-0'>
				<div class='row'>
					{teams && teams.map(team => (
						<div class='col-6 mb-2'>
							<div class={`border rounded rounded-pill px-3 py-2 ${currentTeam === team.id ? 'bg-success' : ''}`}
								style='line-height: 0.9em; cursor: pointer; pointer-events: inherit;'
								onClick={() => this.toggleTeam(team.id)}
							>
								<small>
									<i class='fas fa-users mr-2' />
									{team.name}
								</small>
							</div>
						</div>
					))}
				</div>
			</div>
        );
    }
}

@observer
class Workouts extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

	loadWorkouts = async () => {
		this.setState({ isLoading: true });
		const { workoutStore } = this.props.stores;
		const { currentTeam } = workoutStore;
		await workoutStore.load({ query: { team: currentTeam } });
		await this.loadWorkoutSummary();
		this.setState({ isLoading: false });
	}

	loadDogs = async () => {
		const { dogStore } = this.props.stores;
		const { currentTeam } = dogStore;

		await dogStore.load({ query: { team: currentTeam }, addData: ['workoutSummary'] });
	}

	async loadTracks() {
		const { trackStore } = this.props.stores;
		await trackStore.load();
	}

	async loadWorkoutSummary() {
		const { userStore, workoutStore } = this.props.stores;
		const { currentTeam } = workoutStore;
        await userStore.getWorkoutSummary({ team: currentTeam });
    }

	componentDidMount() {
		this.loadWorkouts();
		this.loadDogs();
		this.loadTracks();
	}

	render() {
		const { isLoading } = this.state;
		const { userStore, appState, workoutStore } = this.props.stores;
		const { darkmode } = appState;
		const workouts = workoutStore.getLatests();
		const {
			workoutSummaryWeek = [],
			graphWorkoutSummaryWeekDistanceCurrent, graphWorkoutSummaryWeekDistancePrev,
			graphWorkoutSummaryWeekElevationCurrent, graphWorkoutSummaryWeekElevationPrev,
		} = userStore;
		return (
			<div class='container-fluid' style='margin-bottom: 100px; margin-top: 90px;'>
				<div class='row mb-4 fixed-top' style={`margin-top: 58px; background-color: ${darkmode ? '#191d21' : '#f8f9fa'};`}>
					<div class='col text-center border-bottom border-primary'>
						<a href='/workouts/' class='text-primary stretched-link'><i class='fas fa-chart-line' /> Oversikt</a>
					</div>
					<div class='col text-center'>
						<a href='/workouts/list' class='text-secondary stretched-link'><i class='fas fa-list' /> Liste</a>
					</div>
					<div class='col text-center'>
						<a href='/workouts/plan' class='text-secondary stretched-link'><i class='fas fa-calendar-alt' /> Plan</a>
					</div>
				</div>
				
				<div class='row'>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 border-bottom'>
						<Teams stores={this.props.stores} callback={this.loadWorkouts} />
					</div>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-2 mb-2'>
						<a class='btn btn-primary btn-sm float-right'
							href='/workouts/edit/new'
						>
							<i class='fas fa-plus' /> Legg til treningstur
						</a>
					</div>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
						{workoutSummaryWeek && workoutSummaryWeek.length > 0 && (
							<>
								<h5>Trenings statistikk</h5>
								<div class='w-100 mt-3 mb-4'>
									<small class='mb-4'><small>
										<table class='table table-sm font-weigth-lighter border-bottom'>
											<thead>
												<tr>
													<th style='padding: .2rem;'>Mnd</th>
													{util.range(1, 12).map(w => (
														<th class='text-right' style='padding: .2rem;'>{MONTHS[w - 1]}</th>
													))}
												</tr>
											</thead>
											<tbody>
												<tr>
													<th scope='row' style='padding: .2rem;'>2019</th>
													{util.range(1, 12).map(m => (
														<>
															<td class='text-right' style='padding: .2rem;'>
																{userStore.findWorkoutSummaryMonth(2019, m)}<br />
																{userStore.findWorkoutSummaryMonth(2019, m, 'elevation')}<br />
															</td>
														</>
													))}
													<td class='text-muted' style='padding: .2rem;'>
														<nobr><i class='fas fa-road' /> km</nobr><br />
														<nobr><i class='fas fa-mountain' /> m</nobr><br />
													</td>
												</tr>
												<tr>
													<th scope='row' style='padding: .2rem;'>2020</th>
													{util.range(1, 12).map(m => (
														<>
															<td class='text-right' style='padding: .2rem;'>
																{userStore.findWorkoutSummaryMonth(2020, m)}<br />
																{userStore.findWorkoutSummaryMonth(2020, m, 'elevation')}<br />
															</td>
														</>
													))}
													<td class='text-muted' style='padding: .2rem;'>
														<nobr><i class='fas fa-road' /> km</nobr><br />
														<nobr><i class='fas fa-mountain' /> m</nobr><br />
													</td>
												</tr>
											</tbody>
										</table>
									</small></small>
												
									<LineGraph
										stores={this.props.stores}
										width={500}
										height={150}
										showXTicks={1}
										xTicksVal={val => `uke ${val}`}
										paddingTop={30}
										lineFromXzero={true}
							            lineFromXmax={true}

										dataLeft={[graphWorkoutSummaryWeekDistanceCurrent, graphWorkoutSummaryWeekDistancePrev]}
										legendLeft={['2020 Distanse', '2019 Distanse']}
										// yMaxLeft={30}
										// yMinLeft={0}
										showYTicksLeft={1}
										yTicksLeftPostfix={'km'}
										unitLeft={['km', 'km']}
										avgWindowLeft={[0, 0]}
										smoothLeft={[1, 1]}

										dataRight={[graphWorkoutSummaryWeekElevationCurrent, graphWorkoutSummaryWeekElevationPrev]}
										legendRight={['2020 Høydemeter', '2019 Høydemeter']}
										showYTicksRight={1}
										yTicksRightPostfix={'m'}
										unitRight={['m', 'm']}
										avgWindowRight={[0, 0]}
										smoothRight={[1, 1]}

										// marker={week}
									/>
								</div>
							</>
						)}

						{graphWorkoutSummaryWeekDistanceCurrent && graphWorkoutSummaryWeekDistanceCurrent.length < 2 && (
							<div class='mb-5 py-3 border-top border-bottom'>
								<div class='text-left' style='line-height: 1.0em;'>
									<div class='display-4 float-left text-warning mr-3'>
										<i class='fas fa-paw' />
									</div>
									<small>
										Foreløpig er det litt lite data for å kunne vise noen fine grafer. 
										Etterhvert som du legger inn flere treninger så vil dette bli en fin tabell og graf som viser progresjonen din.
									</small>
								</div>
							</div>
						)}
					</div>
				</div>

				<div class='row'>
					{workouts && workouts.length > 0 && <div class='col-12 mb-4'>
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0 mt-4'>		
							<h5>Nyeste treningsturer - {workouts.length} stk</h5>
						</div>
						{workouts && workouts.map(workout => (
							<Workout stores={this.props.stores} workout={workout} />
						))}
					</div>}
				</div>

				<div class='row'>
					{workoutSummaryWeek && workoutSummaryWeek.length > 0 && <div class='col-12 mb-4'>
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0'>
							<h5 class='mt-4'>Treningsoversikt</h5>
						</div>
						{/* {JSON.stringify(workoutSummaryWeek)} */}
						{workoutSummaryWeek && workoutSummaryWeek.map(summary => (
							<WorkoutSummary stores={this.props.stores} summary={summary} />
						))}
					</div>}
				</div>
			</div>
		);
	}
}

export default Workouts;
